.rolls_container {
  max-width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-style: normal;
  font-weight: normal;
  color: #000000;
  background: #FFFFFF;
  font-size: 18px;
  line-height: 22px;

  &_block {
    width: 100%;
    display: flex;
    justify-content: space-around;

    @media (max-width: 370px) {
      justify-content: space-around;
    }

    flex-wrap: wrap;
    padding-top: 40px;

    h3 {
      text-align: center;
      margin-top: -100px;
      font-size: 20px;
    }

    // .container {
    //   width: 50% !important;
    // }
  }
}

@media screen and (max-width: 600px) {
  .rolls_container {
    &_block {
      padding-top: 0;

      h3 {
        margin-top: 0;
        font-size: 16px;
      }
    }
  }
}

.page-wrapper_rolls-filters {
  display: flex !important;

  .item {
    @media screen and (max-width: 1060px) {
      height: 40px !important;
      padding: 3px 10px !important;
    }

    p {
      @media screen and (max-width: 1060px) {
        font-size: calc(12px + 0.5vw);
      }
    }
  }
}

.page-wrapper_filters {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;

  .item {
    display: flex;
    align-items: center;
    cursor: pointer;
    margin: 10px;
    height: 60px;
    padding: 3px 15px;
    background-color: #f8f2f2;
    border-radius: 20px;
    transition: background-color 0.2s ease;

    div {
      img {
        max-width: 30px;
        max-height: 30px;
        margin-right: 10px;
      }
    }

    p {
      transition: color 0.2s ease;
      color: rgb(99, 91, 91);
    }
  }

  .item:hover {
    background-color: #e7d9d9;

    p {
      color: #000000;
    }
  }

  .active {
    background-color: #f8f2f2;
    border: 2px solid rgb(245, 187, 80);

    p {
      color: rgb(0, 0, 0);
    }
  }

  .active:hover {
    background-color: #f8f2f2;
  }
}

@media screen and (max-width: 1060px) {
  .page-wrapper_filters {
    display: none;
  }
}